*
{
    margin:0;
    padding:0;
}
.home
{
    background-image: url('https://www.psdgraphics.com/wp-content/uploads/2020/07/blue-open-sea-background-768x432.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height:100vh;
    align-items: center;  
}
.home__title{
    font-size:15px;
}
.home__container
{
    height:10%;
    display:flex;
    flex-direction:column;
    justify-content: center;
    
}
.home__formContainer {
    margin-top: 500px;
    margin-left:500px;
    padding:10px;
    border-radius:5px;
    width:100px;
    align-items:center;
    /* box-shadow:1px 10px 15px -2px rgba(0,0,0,4.3); */
}
 .home__formContainer input{
    background: transparent;
    border:1px solid white;
    outline:none;
